import { swiper, swiperSlide } from 'vue-awesome-swiper';
import {mapState} from 'vuex';
export default {
    props: ['imgSrcArr'],
    data() {
        return {
            // 
            swiperOption: {
        // notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
                notNextTick: true,
        // swiper configs swiperapi
                autoplay: true,
        // direction : 'vertical', //
                effect: 'effect', // 
                grabCursor: false, // 
                setWrapperSize: true, //  
                // autoHeight: true, // 
        // paginationType:"bullets",
                pagination: {
                    el: '.swiper-pagination',  //   
                    clickable: true // 
                },
                prevButton: '.swiper-button-prev',
                nextButton: '.swiper-button-next',
        // scrollbar:'.swiper-scrollbar',
                observeParents: true
            },
            newImgSrcArr: ['/Public/Uploads/20180528/5b0bb30255d5a.jpg', '/Public/Uploads/20180528/5b0bb300010f7.jpg']
        };
    },
    computed: {
        ...mapState(['global'])
    },
    watch: {
        imgSrcArr: function() {
            this.loadSwiper();
        }
    },
    methods: {
        loadSwiper() {
            if (this.imgSrcArr === undefined) {
                this.imgSrcArr = [];
            } else {
                if (Object.prototype.toString.call(this.imgSrcArr).slice(8, -1) === 'String') {
                    this.newImgSrcArr.push(this.imgSrcArr);
                } else {
                    this.newImgSrcArr = this.imgSrcArr;
                }
            }
        }
    },
    created() {
        this.loadSwiper();
    },
    components: {
        swiper,
        swiperSlide
    }
};



